import React from 'react';
import './Product.css';
import classes from './Product.css';
const Product=(props)=> {
    const currenthour = new Date().getHours() > 9 ? new Date().getHours() : '0' + new Date().getHours();
    const Currentminutes = new Date().getMinutes() > 9 ? new Date().getMinutes() : '0' + new Date().getMinutes();
    const currentsecond = new Date().getSeconds() > 9 ? new Date().getSeconds() : '0' + new Date().getSeconds();

    return(
        <div className="Product">
            <img id="img_color" src="https://imgur.com/iOeUBV7.png" alt="red_strap"></img>
    

     <div className="featuredata">
        <p>{`${currenthour}:${Currentminutes}:${currentsecond}`}</p>
  </div>
    {/*<div className="heartrate">
        <img className="herTimg" src="hert.png" alt="heart"></img>
        <p>78</p>
    </div>*/}
        </div>
    )
}
export default Product;
